<script setup lang="ts">
import { onBeforeUnmount, onMounted, ref } from 'vue';

import TheLeftModal from '../components/TheModalLeft.vue';
import MenuModal from '../components/MenuModal.vue';
import Portfolio from '../components/Portfolio.vue';

const showMenu = ref(false);
const scrollLock = ref(false);

const hideContactBtn = ref(false);
const hideLodo = ref(false);

const hideHandler = () => {
  const block = document.getElementById('about');
  const top = block?.getBoundingClientRect().top;

  if (top && top < 60) {
    hideContactBtn.value = true;
  } else {
    hideContactBtn.value = false;
  }

  if (top && top < 80) {
    hideLodo.value = true;
  } else {
    hideLodo.value = false;
  }
}

const scrollTop = () => {
  const options: ScrollToOptions = {
    top: 1,
    behavior: 'smooth'
  }

  window.scrollTo(options);
}

onMounted(() => {
  hideHandler();
  window.addEventListener('scroll', hideHandler)
})

onBeforeUnmount(() => {
  window.removeEventListener('scroll', hideHandler)
})
</script>
