.hover_effect {
  cursor: pointer;
  display: block;
  width: fit-content;
  padding: 1px;
  position: relative;
  z-index: 2;
  text-decoration: none;
  box-sizing: border-box;
  background: transparent;
}

.hover_effect:after {
  content: "";
  height: 3px;
  left: 0;
  width: 1px;
  position: absolute;
  transition: width 1s ease, background-color 1s ease;
  right: 0;
  bottom: -5px;
  margin: 0 auto;
